/* Global Styles & Variables */
:root {
  /* Colors */
  --color-white: #ffffff;
  --color-grey-white: #f7f6ee;
  --color-light-grey: #bfbfbf;
  --color-grey: #949494;
  --color-rich-Burgundy: #7b1e3a;
  --color-light-yellow: #fffec3;
  --color-Soft-Gold: #daa49a;
  --color-Creamy-Beige: #ead2ac;
  --color-Dark-Charcoal: #333333;
  --color-Vintage-Olive: #587d71;
  --color-black: #000000;
  /* Font Sizes */
  --font-large: 2.5rem;  /* Responsive font size using rem */
  --font-medium: 1.375rem;
  --font-regular: 0.9375rem;
  --font-small: 0.75rem;
  --font-micro: 0.625rem;
  /* Font Weights */
  --weight-bold: 700;
  --weight-semi-bold: 600;
  --weight-regular: 400;
  /* Borders and Radius */
  --size-border-radius: 4px;
  /* Animation Speed */
  --animation-duration: 300ms;
}

/* Layout */

.main__container {
  display: flex;
  flex-direction: row;
  padding: 20px;
  background-color: var(--color-grey-white);
}

.search__container {
  display: flex;
  flex-direction: column;
  align-items: center;
  min-width: 100%;
  max-width: 800px;  /* Maximum width of the search container */
  margin: auto;  /* Center align the container */
  padding: 20px;
  background-color: var(--color-grey-white);
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  border-radius: var(--size-border-radius);
}

/* Form input and buttons */
.searchbar__container {
  display: flex;
  width: 100%;
}

.mainLogo {
  width: 40%;
  height: 40%;
}

input[type="text"], .search__button {
  flex-grow: 1;
  margin: 5px;
  padding: 10px;
  border-radius: var(--size-border-radius);
}

input[type="text"] {
  flex-basis: 90%;  /* Text input takes more space */
}

.search__button {
  flex-basis: 20%;  /* Button takes less space */
  background-color: var(--color-rich-Burgundy);
  color: var(--color-white);
  border: none;
  cursor: pointer;
}

.search__button:hover {
  background-color: var(--color-Soft-Gold);
}


/* Dropdown */
.dropdown {
  width: 100%; /* Match width with input */
  max-height: 200px;
  overflow-y: auto; /* Scrollable dropdown */
  position: relative; /* Position it below the search bar */
  top: 100%;  /* Start right after the search bar */
  left: 0;
  background-color: var(--color-white);
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  border-radius: var(--size-border-radius);
  z-index: 1000;
}

.dropdown-content {
  padding: 8px;
  cursor: pointer;
  transition: background-color var(--animation-duration) ease;
}

.dropdown-content:hover {
  background-color: var(--color-Creamy-Beige);
}

/* sidebar controls */
.sidebar__container {
  position: fixed;
  right: 0;
  display: flex;
  flex-direction: column;
  height: fit-content;
  background-color: var(--color-grey-white);
}

.sort__container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  border-bottom: 1px solid var(--color-light-grey); /* Responsive line */
}

.sort__container ul li {
  cursor: pointer;
  transition: background-color var(--animation-duration) ease;
  font-size: var(--font-small);
  padding-bottom: 3px;
  border-bottom: 0.7px dotted var(--color-light-grey);
}

.sort__container ul li:hover {
  background-color: var(--color-light-yellow);
}

/* Wine Results */
.wine__results {
  margin-top: 20px;
  color: var(--color-Vintage-Olive);
}

.wine__results > div {
  padding: 10px;
  border-bottom: 1px solid var(--color-light-grey); /* Responsive line */
}

.goShop {
  display: flex;
}

.goShop button{
  margin-left: auto;
  padding: 10px;
  border-radius: var(--size-border-radius);
  background-color: var(--color-rich-Burgundy);
  color: var(--color-white);
  border: none;
  cursor: pointer;
}

.goShop button:hover {
  background-color: var(--color-Soft-Gold);
}

/* Pagination */
.pagination {
  display: flex;
  justify-content: center;
  margin-top: 20px;
  flex-wrap: wrap; /* Wrap items in small screens */
}

.pagination button {
  min-width: 40px; /* Minimum width to accommodate single digits */
  padding: 5px 10px; /* Adjusted padding for better fit */
  margin: 5px;
  background-color: var(--color-rich-Burgundy);
  color: var(--color-white);
  font-size: var(--font-regular); /* Ensure font size is not too large */
  text-align: center; /* Ensure text is centered */
  border: none;
  border-radius: var(--size-border-radius);
  cursor: pointer;
  display: flex;
  align-items: center; /* Vertically center text */
  justify-content: center; /* Horizontally center text */
}

.pagination button:hover {
  background-color: var(--color-Soft-Gold);
}

.pagination button:disabled {
  background-color: var(--color-light-grey);
  cursor: not-allowed;
}

.pagination button.active {
  background-color: var(--color-Dark-Charcoal); /* Darker color for active page */
  color: var(--color-white);
}

/* Responsive adjustments */
@media (max-width: 1024px) {
  .sidebar__container {
    width: 25%; /* Increase relative width on smaller desktop screens */
  }
}


/* Media Queries */
@media (max-width: 768px) {
  .search__container {
    padding: 10px;
    border-radius: 2px;
  }

  input[type="text"], .search__button {
    padding: 8px;
  }

  .dropdown {
    max-height: 150px;
  }

  .pagination button {
    padding: 3px 8px; /* Smaller padding on smaller screens */
    min-width: 30px; /* Smaller minimum width on smaller screens */
  }

  .sidebar__container {
    width: 20%; /* Further increase width on tablet screens */
  }
}

@media (max-width: 480px) {
  .sidebar__container {
    transform: translateX(100%); /* Hide sidebar off the right edge */
    position: fixed;
    width: 80%; /* Make sidebar wider when it is shown */
  }

  .sidebar__toggle {
    display: block;
    position: fixed;
    right: 10px;
    top: 10px;
    z-index: 1600; /* Above the sidebar */
    cursor: pointer;
    background-color: var(--color-rich-Burgundy);
    color: var(--color-white);
    border: none;
    border-radius: 50%;
    width: 50px;
    height: 50px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
  }

  /* Styling for when sidebar is visible */
  .sidebar__container.open {
    transform: translateX(0); /* Bring sidebar into view */
  }
}

